import { makeStyles } from '@material-ui/core/styles'
import { useState } from 'react'
import { iconInfo, TotalValueIcon, GlobalIcon } from '@/assets/svg'
import { textInfo } from '@/config'
import { FloatMessage2 } from '@/components/Modules'
import DynamicFont from 'react-dynamic-font'

const useStyles = makeStyles({
  root: {},
  totalValue: {
    display: 'flex',
    alignItems: 'center',
    '&>div': {
      '&:first-child': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        width: '180px',
        margin: '10px 0',
        fontSize: '20px',
        fontFamily: 'Gillsans',
        color: '#fff',
        '&>div': {
          display: 'flex',
          '&>img': {
            width: '20px',
          },
          '&>span': {
            marginLeft: '10px',
          },
        },
        '&>img': {
          width: '14px',
        },
      },
      '&:last-child': {
        fontSize: '24px',
        fontFamily: 'Gillsans',
        color: '#4975FF',
        marginLeft: '30px',
      },
    },
    '@media screen and (max-width:960px)': {
      flexDirection: 'column',
      alignItems: 'start',
    },
  },
  globalValue: {
    fontFamily: 'Gillsans',
    display: 'flex',
    alignItems: 'start',
    margin: '10px 0',
    '&>div': {
      '&:first-child': {
        fontSize: '20px',
        color: '#fff',
        display: 'flex',
        alignItems: 'center',
        width: '180px',
        '&>img': {
          width: '20px',
        },
        '&>span': {
          marginLeft: '10px',
        },
      },
      '&:last-child': {
        display: 'flex',
        justifyContent: 'space-between',
        marginLeft: '30px',
        width: '710px',
        '@media screen and (max-width:960px)': {
          width: 'calc(100% - 30px)',
        },
      },
    },
    '@media screen and (max-width:960px)': {
      flexDirection: 'column',
      alignItems: 'start',
    },
  },
  globalValueInfo: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    width: 'calc(50% - 10px)',
    '&>div:first-child': {
      color: '#7B96EB',
      marginBottom: '5px',
      width: '100%',
    },
    '&>div:last-child': {
      color: 'white',
      backgroundColor: '#111C3C',
      padding: '6px',
      width: '100%',
      boxSizing: 'border-box',
    },
  },
})

export default function Global({ totalValueLocked, totalBorrowed, totalCollateral }) {
  const classes = useStyles()
  const [anchorEl, setAnchorEl] = useState({ totalValue: null })

  return (
    <div className={classes.root}>
      <div className={classes.totalValue}>
        <div>
          <div>
            <img src={TotalValueIcon} alt="" />
            <span>Total Value Locked</span>
          </div>
          <img
            src={iconInfo}
            alt=""
            onMouseEnter={(e) => setAnchorEl({ totalValue: e.currentTarget })}
            onMouseLeave={() => setAnchorEl({ totalValue: null })}
          />
          <FloatMessage2 anchorEl={anchorEl['totalValue']} info={textInfo['TotalValueLocked']} />
        </div>
        <div>${totalValueLocked || 0}</div>
      </div>

      <div className={classes.globalValue}>
        <div>
          <img src={GlobalIcon} alt="" />
          <span>Global</span>
        </div>
        <div>
          <div className={classes.globalValueInfo}>
            <div>
              <DynamicFont content="Total Borrowed" />
            </div>
            <div>
              <DynamicFont content={`$${totalBorrowed || 0}`} />
            </div>
          </div>
          <div className={classes.globalValueInfo}>
            <div>
              <DynamicFont content="Total Collateral" />
            </div>
            <div>
              <DynamicFont content={`$${totalCollateral || 0}`} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
